










































































































































import uniIcon from '../../components/uni-icon.vue';
import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue';
import cityData from '../../common/city.data.js';

export default {
	data() {
		return {
			mulLinkageTwoPicker: cityData,
			pickerValueDefault: [0, 0],
			themeColor: '#44b549',
			mode: '',
			deepLength: 1,
			showState: {
				top: false,
				middle: false,
				bottom: false,
				bottom1: false
			},
			showMask: false,
			activePop: 'middle',
			attrs: [
				{
					value: '0',
					name: '在校学生'
				},
				{
					value: '1',
					name: '宝妈宝爸',
					checked: 'true'
				},
				{
					value: '2',
					name: '上班族'
				},
				{
					value: '3',
					name: '职场白领'
				},
				{
					value: '4',
					name: '企业高管'
				},
				{
					value: '6',
					name: '自由职业'
				}
			],
		};
	},
	components: {
		uniIcon,
		mpvuePicker,
		mpvueCityPicker
	},
	methods: {
		formSubmit: function (e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		},
		formReset: function () {
			console.log('form发生了reset事件')
		},
		onCancel(e) {
			console.log(e)
		},
		onConfirm(e) {
			this.pickerText = JSON.stringify(e)
		},
		// 二级联动选择
		showMulLinkageTwoPicker() {
			this.pickerValueArray = this.mulLinkageTwoPicker
			this.mode = 'multiLinkageSelector'
			this.deepLength = 2
			this.pickerValueDefault = [0, 0]
			this.$refs.mpvuePicker.show()
		},
		toSelectTrade: function() {
			uni.navigateTo({
				url: '../selectTrade/selectTrade'
			})
		},
		onAttrTrue() {
			this.hide();
			var attrValue = '';
			this.values.forEach(item => {
				console.log(item);
				attrValue = attrValue + ' ' + item
			})
			this.attrValue = attrValue;
		},
		show(e) {
			var pos = e.currentTarget.dataset.position;
			console.log(pos);
			switch (pos) {
				case 'top':
					this.activePop = 'top'
					break
				case 'bottom':
					this.activePop = 'bottom'
					break
				case 'bottom1':
					this.activePop = 'bottom1'
					break
				default:
					this.activePop = 'middle'
			}
			this.showMask = true
			this.showState[this.activePop] = true
		},
		onTradeCancel(e) {
			this.hide()
		},
		hide() {
			this.showMask = false
			this.showState[this.activePop] = false
		},
		checkboxChange: function (e) {
			var values = e.detail.value;
			this.values = values;	
		},
		toChoosewx: function(){
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera', 'album'], //从相册选择
				success: function (res) {
					this.avatar = res.tempFilePaths;
					console.log(this.avatar);
					// console.log(JSON.stringify(res.tempFilePaths));
				}
			});
		}
	}
}



